<template>
    <el-container class="container">
     <el-header>
         <div class="head">
             <img src="../assets/heima.png" alt="">
             <span>电商管理系统</span>
         </div>
        <el-button type="info" @click = 'back'>退出</el-button>
    </el-header>
     <el-container>
      <el-aside :width="collapse ? '64px':'200px'" >
          <div class='toggle-button' @click='checkout'>|||</div>
         <el-menu background-color="#333744"
                    text-color="#fff"
                    active-text-color="#409eff"
                    unique-opened
                    :collapse='collapse'
                    router
           >
         <el-submenu :index="item.id + ''" v-for = 'item in menuList' :key = 'item.id'>
        <!--一级菜单-->
          <template slot="title">
              <i :class="iconLits[item.id]"></i>
              <span>{{item.authName}}</span>
         </template>
         <!--二級菜單-->
         <el-menu-item :index = "'/' + subItem.path" v-for = 'subItem in item.children' :key = 'subItem.id'>
               <template slot="title">
               <i class="el-icon-menu"></i>
               <span slot="title">{{subItem.authName}}</span>
               </template>
        </el-menu-item>
       </el-submenu>
    </el-menu>
  </el-aside>
  <el-main>
      <!--路由占位符-->
      <router-view></router-view>
  </el-main>
</el-container>
</el-container>
</template>
<script>
export default {
    data(){
        return{
          menuList:[],
          iconLits:{
              '125':'iconfont icon-user',
              '103':'iconfont icon-tijikongjian',
              '101':'iconfont icon-shangpin',
              '102':'iconfont icon-danju',
              '145':'iconfont icon-baobiao'
          },
          collapse:false
        }
    },
  created(){
    this.getEmnuDate();
  },
  methods: {
    back() {
      //退出的时候清除sessionStorage的token,并跳转到登录页面，在下次登录的时候又添加上token
      window.sessionStorage.clear();
      this.$router.push("/login");
    },
    async getEmnuDate(){
        //请求菜单栏数据
        const {data:res} = await this.$http.get('menus');
       
        //若请求失败，则发送错误message
        if(res.meta.status !== 200) return this.$Message.error(res.meta.msg) ; 
        //将获取到的菜单栏数据存储到menuList
        this.menuList = res.data;
        console.log(this.menuList)
    },
    //切换菜单栏函数
    checkout(){
        this.collapse = !this.collapse;
    }
  },
};
</script>
<style lang="less" scoped>
.container {
  height: 100%;
}
.el-header {
  background-color: #373d41;
  display: flex;
  justify-content: space-between;
  padding-left: 0;
  align-items: center;
  color: white;
  div {
    display: flex;
    align-items: center;
    span {
      margin-left: 15px;
    }
  }
}
.el-aside {
  background-color: #333744;
  .toggle-button{
      background-color: rgb(128, 124, 124);
      color: #fff;
      text-align: center;
      line-height: 24px;
      font-size: 10px;
      letter-spacing: 0.2em;
  }
  .el-menu{
      border-right: 0;
  }
}
.el-main {
  background: #eaedf1;
}
.iconfont{
    margin-right: 10px;
}
</style>
